.container{

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 500px;
}

.login{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 100vh;
    width: 100%;
    color: white;
}

.login-logo img{
    width: 300px;
    height: 300px;
}

.login-eye{
    background: none;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.login-top h1{
    font-family: 'Roboto';
    font-size: 50px;
}

.login-top h2{
    font-family: 'Roboto';
    font-weight: 300;
    font-size: 25px;
    font-weight: 500;
    color: #e1e1e6;
}


.loginInputEmail, .loginInputPassword{
    display: flex;
    align-items: center;
    color: white;
    background-color: #1A1A1D;
    margin-block: 5px;
    border-radius: 5px;
    padding: 3px;
    width: 98%;
    height: 50px;   
}

.loginInputEmail svg, .loginInputPassword svg{
    margin-left: 10px;
    font-size: 25px;
    background: none;
}


.loginInputEmail input, .loginInputPassword input{
    background: transparent;
    width: 100%;
    outline-width: 0;
    color: white;
    border: none;
    font-size: 17px;
    margin-inline: 10px;
}

.login-top{
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 15px;
    align-items: center;
    width: fit-content;
    border: 1px solid #06936C;
    
}

.login-botton{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    gap: 10px;
    border: 1px solid #06936C;
}


.login-top button, .login-botton button{
    background-color: #202024;
    color: #EFF2F5;
    font-weight: 800;
    width: 90%;
    height: 50px;
    border-radius: 5px;
    font-size: 18px;
    margin-top: 5px;
    border: none;

    outline-width: 0;
    
}

.login-botton button:hover, .login-top button:hover{
    background-color: #29292E;
    color: #EFF2F5;
    cursor: pointer;
    transition: .8s;
}


